/*
  ./src/scss/ieps_framework/modules/_menus.scss
  Module des menus
 */

 // MENU SLIDER
 @each $etiquette, $dimension in $breakpoints {
   @media screen and (min-width: $dimension){
     .menu-#{$etiquette}-slider {
       // Valeurs par défaut
       ul {
         list-style: none;
         margin: 0;
         padding: 0;
         a {
           text-decoration: none;
         }
       }
       // Le bouton
       .menu-slider-btn {
         position: $menuSliderBtnPosition;
         display: $menuSliderBtnDisplay;
         top: $menuSliderBtnTop;
         left: $menuSliderBtnLeft;
         background: $menuSliderBtnFondCouleur;
         color: $menuSliderBtnTexteCouleur;
         padding: $menuSliderBtnPadding;
         &:hover {
           background: $menuSliderBtnHoverFondCouleur;
           color: $menuSliderBtnHoverTexteCouleur;
         }
       }
       // Le menu
       .menu-slider-menu {
         position: fixed;
         top: 0;
         top: -$menuSliderMenuLargeur;
         background: $menuSliderMenuFondCouleur;
         width: $menuSliderMenuLargeur;
         height: $menuSliderMenuHauteur;
         transition: top 300ms ease-in-out;
         a {
           display: block;
           background: $menuSliderLiensFondCouleur;
           color: $menuSliderLiensTexteCouleur;
           padding: $menuSliderLiensPadding;
           &:hover {
             background: $menuSliderLiensHoverFondCouleur;
             color: $menuSliderLiensHoverTexteCouleur;
           }
         }
         &.visible {
           top: 0;
         }
       }
     }
   }
 }

 // MENU HORIZONTAL
 @each $etiquette, $dimension in $breakpoints {
   @media screen and (min-width: $dimension){
     .menu-#{$etiquette}-horizontal {
       // Valeurs par défaut
       ul {
         list-style: none;
         padding: 0;
         margin: 0;
       }
       a {
         display: block;
         text-decoration: none;
       }
       .menu-slider-btn {
         display: none;
       }
       .menu-slider-menu {
         position: static;
         background: none;
         width: auto;
         height: auto;
       }
       // Premier niveau
       > ul {
         display: flex;
         justify-content: $menuHorizLiensAlignement;
         > li {
           flex-grow: $menuHorizLiensGrow;
           margin-right: $menuHorizLiensMarge;
           text-align: $menuHorizLiensTexteAlignement;
           position: relative;
           &:last-of-type {
             margin-right: 0;
           }
           &.actif {
             > a {
               background: $menuHorizLiensActifFondCouleur;
               color: $menuHorizLiensActifTexteCouleur;
             }
           }
           > a {
             background: $menuHorizLiensFondCouleur;
             color: $menuHorizLiensTexteCouleur;
             padding: $menuHorizLiensPadding;
             &:hover {
               background: $menuHorizLiensHoverFondCouleur;
               color: $menuHorizLiensHoverTexteCouleur;
             }
           }
         }
       }
       // MENUS DROPDOWN
         .menu-dropdown-menu {
           position: absolute;
           width: 100%;
           max-height: 0;
           overflow: hidden;
           transition: max-height 300ms ease-in-out;
           &.visible {
             max-height: 3000px;
           }
           a {
             background: $menuDropDownLiensFondCouleur;
             color: $menuDropDownLiensTexteCouleur;
             padding: $menuDropDownLiensPadding;
             &:hover {
               background: $menuDropDownLiensHoverFondCouleur;
               color: $menuDropDownLiensHoverTexteCouleur;
             }
           }
         }
     }
   }
 }
