.card {
   width: 100%;
     // margin: 1em;
     // border: solid ;
     border: 1px solid $couleurNeutreFoncee;
     border-radius: 3px;
 }

 .card-img {
    width: 100%;
    border-radius: 3px 3px 0 0;

 }

 .card-body {
   margin: 0px;
  padding: 1em;

 }

 .card-title {
   text-align:left;
   font-size: 1.5em;
   margin: auto;

 }

 .card-text {

 }

 .card-footer {
    background: $couleurNeutre;
    padding: 1em;
    border-top: 1px solid $couleurNeutreFoncee;
 }
