/*
  ./src/scss/ieps_framework/_variables.css
  Variables du framework IEPS CSS
 */
  // COULEURS -------------------------------------
    $couleur1: #057BFF;
    $couleur1Claire: lighten($couleur1, 30%);
    $couleur1Foncee: darken($couleur1, 30%);
    $couleur2:#343A40;
    $couleur2Claire: lighten($couleur2, 30%);
    $couleur2Foncee: darken($couleur2, 30%);
    $couleurNeutre: #eee;
    $couleurNeutreClaire: lighten($couleurNeutre, 30%);
    $couleurNeutreFoncee: darken($couleurNeutre, 5%);

  // MODULE DES BOITES ----------------------------
    $boiteFixeDimension: 1000px;

  // BORDS DES BOITES -----------------------------
    $boiteBordsCouleur: $couleurNeutre;
    $boiteBordsDimension: 1px;
    $boiteBordsPadding: 1em;

  // BOITES FLOTTANTES ----------------------------
    $boiteFlottanteLargeur: 200px;
    $boiteFlottanteMargin: 1em;

  // MODULE DE LA grid --------------------------
    $gridNombreColonnes: 12;
    $gridgap:      1em;

    $breakpoints: (
        'xs' : '0',
        's'  : '600px',
        'm'  : '800px',
        'l'  : '1000px',
        'xl' : '1200px',
        );

  // MODULE DES MENUS -----------------------------
  // MENU HORIZONTAL
   // Le menu
    $menuHorizLiensAlignement: left;
    $menuHorizLiensGrow: 1;
    $menuHorizLiensMarge: 1px;
    $menuHorizLiensTexteAlignement: center;

   // Les liens
    $menuHorizLiensFondCouleur: $couleur2;
    $menuHorizLiensTexteCouleur: $couleurNeutre;
    $menuHorizLiensPadding: .5em;
    $menuHorizLiensHoverFondCouleur: darken($couleur2, 5%);
    $menuHorizLiensHoverTexteCouleur: $couleurNeutre;
    $menuHorizLiensActifFondCouleur: darken($couleur2, 15%);
    $menuHorizLiensActifTexteCouleur: $couleurNeutre;

   // Les sous-menus dropdown
    $menuDropDownLiensFondCouleur: lighten($couleur2, 10%);
    $menuDropDownLiensTexteCouleur: $couleurNeutre;
    $menuDropDownLiensPadding: .5em;
    $menuDropDownLiensHoverFondCouleur: lighten($couleur2, 15%);
    $menuDropDownLiensHoverTexteCouleur: $couleurNeutre;

  // MENU SLIDER
   // Le bouton
    $menuSliderBtnTop: 1em;
    $menuSliderBtnLeft: 4em;
    $menuSliderBtnFondCouleur: $couleur2;
    $menuSliderBtnTexteCouleur: $couleurNeutre;
    $menuSliderBtnPadding: .5em;
    $menuSliderBtnHoverFondCouleur: darken($couleur2, 20%);
    $menuSliderBtnHoverTexteCouleur: $couleurNeutre;
    $menuSliderBtnDisplay: inline;
    $menuSliderBtnPosition: static;

   // Le menu
    $menuSliderMenuLargeur: 100%;
    $menuSliderMenuHauteur: auto;
    $menuSliderMenuFondCouleur: $couleur2;

   // Les liens
    $menuSliderLiensFondCouleur: $couleur2;
    $menuSliderLiensTexteCouleur: $couleurNeutre;
    $menuSliderLiensPadding: .5em;
    $menuSliderLiensHoverFondCouleur: darken($couleur2, 10%);
    $menuSliderLiensHoverTexteCouleur: $couleurNeutre;
