/*
  ./src/scss/ieps_framework/modules/_grid.scss
  Module de la grid
 */

// LE CONTENEUR -----------------------------------
  .grid {
    display: grid;
    grid-template-columns: repeat($gridNombreColonnes, 1fr);
    &.grid-gap {
      grid-gap: $gridgap;
    }
  }

// TOUS LES ITEMS A $gridNombreColonnes COLONNES PAR DEFAUT
  .grid > * {
    grid-column: span $gridNombreColonnes;
  }

  @each $etiquette, $dimension in $breakpoints {
    @media screen and (min-width: $dimension){
      @for $i from 1 through $gridNombreColonnes {
        .#{$etiquette}#{$i} {
          grid-column: span $i;
        }
        .#{$etiquette}-r#{$i} {
          grid-row: span $i;
        }
      }
    }
  }
