// ELEMENTS STICKY
.sticky {
   position: sticky;
   top: 0;
   // transition: all 300ms;

   .trigger {
      width: 0;
      height: 0;
      position: absolute;
      top: -1px;
   }
   &.sticked {
   }
}



// FRAMES YOUTUBE

 .youtubeContainer {
     position: relative;
     width: 100%;
     height: 0;
     padding-bottom: 56.25%;

    > iframe {
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
     }
 }
